import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    temporaryChapters: localStorage.getItem('allChapters') ? JSON.parse(window.localStorage.getItem('allChapters')).chapters : [],
    chaptersIndex: localStorage.getItem('allChapters') ? JSON.parse(window.localStorage.getItem('allChapters')).index : 0,
    myBookRack: JSON.parse(window.localStorage.getItem('myBookRack')) || [],
    // pageNum: localStorage.getItem('allChapters') ? JSON.parse(window.localStorage.getItem('allChapters')).pageNum : 0, //页码
  },
  mutations: {
    getAllChapter(state, payload) {
      state.temporaryChapters = payload.chapters;
      state.chaptersIndex = payload.index;
      // state.pageNum = payload.pageNum; //页码
    },
    addInBookrack(state, payload) {
      // 如果用户一开始没有书架那么就可以给个[]值
      let mybookArr = JSON.parse(localStorage.getItem("myBookRack")) || [];
      if (mybookArr.find(item => { return item.id === payload })) {
        return;
      }
      let obj = {
        id: payload,
        index: 0,
        chapter: null,
        expire: Date.now() + 24 * 3600 * 1000, //设置时效
        // pageNum: 0, //页码
      };
      localStorage.setItem("myBookRack", JSON.stringify([...mybookArr, obj]));
      state.myBookRack = [...mybookArr, obj];
    },
    delBookrack(state, payload) {
      let mybookArr = JSON.parse(localStorage.getItem("myBookRack"));
      mybookArr = mybookArr.filter((item) => item.id !== payload)
      localStorage.setItem("myBookRack", JSON.stringify([...mybookArr]));
      state.myBookRack = mybookArr;
    },
    updateBookRack(state) {
      state.myBookRack = JSON.parse(window.localStorage.getItem('myBookRack'));
    },
    updateChaptersIndex(state, payload) {
      // console.log(state.chaptersIndex, payload);
      state.chaptersIndex = payload;
    }
  },
  actions: {},
  modules: {}
})
